const api = ''; // for production use => paths are relative

const uploadImg = async (file, name) => {
  const form = new FormData();
  form.append('avatar', file, name);
  const res = await fetch(`${api}/avatar/upload`, {
    method: 'POST',
    body: form
  });
  return !res.error ? name : res.error;
};
export default uploadImg;
